.theme-loader-overlay {
  width: 100vw;
  height: 100vh;
  background-color: var(--bs-primary1);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  position: fixed;
  top: 0;
  left: 0;
}
.theme-loader-spinner {
  width: 80px;
  height: 80px;
  border: 8px solid;
  border-radius: 50%;
  border-top-color: var(--bs-primary1);
  border-right-color: var(--bs-primary2);
  border-bottom-color: var(--bs-secondary1);
  border-left-color: var(--bs-primary3);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}